.navbar{
  height:80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 20;
  background-color: var(--selected-theme-nav-background-color);

  &__container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80px;
    padding: 0px 20px;

    &__logo{
      color: var(--selected-theme--main-color);
      cursor: pointer;
      font-size: 2rem;
      flex-grow: 1;
    }
    &__menu{
      display: flex;
      list-style: none;
      text-align: center;
      &__item{
        line-height: 40px;
        margin-right: 1.5rem;
        font-size: 1.6rem;
        &::after{
          content: '';
          display: block;
          height: 3px;
          width: 0px;
          background: transparent;
          transition: width 0.5s ease;
        }
        &:hover::after{
          width: 100%;
          background: var(--selected-theme-main-color);
        }

        &__links{
          color: var(--selected-theme-main-color);
          text-decoration: none;
          padding: 0.5rem 1rem;
        }
      }
      &__item:last-child{
        margin:0px;
      }
    }
  }
}
.nav-icon {
  display: none;
}
//navbar my name
.myname{
  color: var(--selected-theme-main-color);
  text-decoration: none;
  padding: 0.5rem 1rem;
}
.name{
  cursor: pointer;
  font-size: 1.25em;
  background: transparent;
  color: var(--selected-theme-main-color);
  border: 1.5px solid var(--selected-theme-main-color);
  border-radius: 4px;
  font-weight: 500;
  text-decoration: none;
  padding: 0.5rem 1rem;
  margin-right: 5px;
  &:hover {
    color: #000000;
    background: var(--selected-theme-main-color);
  }
}
.navbar.active {
  height: 12vh;
  position: fixed;
  top: 0;
  z-index: 1000;
  width: 100%;
  // background-color: #ecf0f3;
  box-shadow: -1px 11px 24px -11px rgba(0, 0, 0, 0.75);
  transition: 0.5s;
}

@media only screen and (max-width: 768px) {
  .navbar__container__menu{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    position: absolute;
    top: 80px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;

    &.active{
      background: #181818;
      left: 0px;
      opacity: 1;
      z-index: 1;
      transition: all 0.5s ease;
    }
  }
  .navbar__container__menu__item{
    border-bottom: 1px solid #282828;
    margin: 0px;
    padding: 20px 0px;
  }
  .nav-icon {
    display: block;
    cursor: pointer;
    color: var(--selected-theme-main-color);
  }
}