p {
  color: var(--selected-theme-main-color);
  font-size: 1.8rem;
  // line-height: 1.65;
  // padding-bottom: 20px;
  border-radius: 1px dashed var(--selected-theme-main-color);
}

span{
  margin-left: 10px;
}

@keyframes rotation {
  100% {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 768px) {
  .about__content {
    flex-direction: column;

    &__servicesWrapper {
      margin-top: 20px;
      margin-bottom: 20px;
      &__innerContent {
        width: 250px;
        height: 250px;
        background: radial-gradient(
          100px,
          var(--selected-theme-main-color),
          transparent 120%
        );

        div:nth-child(1) {
          position: absolute;
          top: -15%;
          left: 38.5%;
        }
        div:nth-child(2) {
          position: absolute;
          right: -11%;
          top: 41%;
        }
        div:nth-child(3) {
          position: absolute;
          bottom: -12%;
          left: 39%;
        }
        div:nth-child(4) {
          position: absolute;
          top: 41.5%;
          left: -12%;
        }
      }
    }
  }
}
