*{
  box-sizing: border-box;
}
.main{
  display: flex;
  flex-direction: column;
}
.sub1{
  display: flex;
  justify-content: space-evenly;
  margin-top: 50px;
}
.sub2{
  margin: auto;
  margin-top: 100px;
  padding:20px;
}
@media only screen and (max-width: 768px) {
  .sub1{
    flex-direction: column;
    justify-content: space-evenly;
    margin-top: 30px;
    gap: 30px;
  }
  .sub2{
    margin-top: 30px;
  }
  h2{
    font-size: 1rem;
  }
}