.contact {
  &__content {
    padding: 20px;
    &__header-text {
      display: inline-block;
      color: var(--selected-theme-sub-text-color);
      font-size: 22px;
      font-weight: 600;
      line-height: 1rem;
      letter-spacing: 0.5px;
      position: relative;
      text-transform: uppercase;
      padding: 5px 10px;
      border-bottom: 2px solid var(--selected-theme-main-color);
    }
    &__header-text::before , &__header-text::after {
      position: absolute;
      content: "";
      width: 2px;
      height: 5px;
      background-color: var(--selected-theme-main-color);
      bottom: -1px;
    }

    &__header-text::before {
      left: 0;
    }
    &__header-text::after {
      right: 0;
    }
    &__form {
      margin-top: 30px;
      &__controlsWrapper {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 40px;
        margin-bottom: 30px;
        div {
          position: relative;
          .inputName,
          .inputEmail,
          .inputDescription {
            width: 100%;
            border: 2px solid var(--selected-theme-main-color);
            padding: 15px 10px;
            transition: all 0.5s ease;
            position: relative;
            z-index: 1;
            background: transparent;
            color: var(--selected-theme-main-color);
          }
          .namelabel, .emaillabel, .descriptionlabel {
            position:absolute;
            font-weight: 500;
            top: 7px;
            left: 12px;
            z-index: 0;
            color: var(--selected-theme-main-color);
            font-size: 16px;
          }
          .inputName:focus,
          .inputEmail:focus,
          .inputDescription:focus {
            outline: none;
          }
          .inputName:focus + .namelabel,
          .inputEmail:focus + .emaillabel,
          .inputDescription:focus + .descriptionlabel {
            font-size: 18px;
            top: -25px;
            left: 0px;
          }
        }
      }
      button {
        cursor: pointer;
        padding: 10px 70px;
        font-size: 2rem;
        background: transparent;
        color: var(--selected-theme-main-color);
        border: 1.5px solid var(--selected-theme-main-color);
        border-radius: 4px;
        font-weight: 500;
        &:hover {
          color: #000000;
          background: var(--selected-theme-main-color);
        }
      }
    }
  }
}
.contact-me__socials-wrapper {
  margin-top: 35px;
  margin-bottom: 15px;
  display: flex;
  gap: 40px;
  svg {
    color: var(--selected-theme-main-color);
    cursor: pointer;
  }
}
.h-data{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap:40px
}
.h-data img{
  width:180px;
  height: 250px;
  border-radius: 10px;
}
p{
  color:var(--selected-theme-main-color);
  
}
.footnote {
  color:var(--selected-theme-main-color);
  font-size: 10pt;
  margin-top: 35px;
  opacity: 0.6;
  text-align: center;
  a{
    color:var(--selected-theme-main-color);
    text-decoration: none;
  }
}
.footnote .highlight {
  color: #e31b6d;
  font-family: "Open Sans";
}
@media only screen and (max-width: 768px) {
  .contact__content__form {
    text-align: center;
    &__controlsWrapper {
      grid-template-columns: repeat(1, 1fr);
      gap: 45px;
    }
  }
  .h-data{
    grid-template-columns: repeat(1, 1fr);
    column-gap: 30px;
  }
  .h-data img{
    width:140px;
    height: 200px;
    border-radius: 8px;
  }
}
