.portfolio {
  &__content {
    padding: 20px;
    width: 100%;
    
    // extra
    margin-bottom: 200px;

    &__filter {
      display: inline-block;
      margin: 0 auto;
      border-radius: 26px;
      border: 1px dashed var(--selected-theme-main-color);
      line-height: 0;
      text-align: center;

      li {
        font-size: 16px;
        display: inline-block;
        cursor: pointer;
        position: relative;
        color: var(--selected-theme-main-color);
        text-transform: uppercase;
        padding: 20px;
        transition: all 0.4s ease;
        font-family: Roboto, sans-serif;
        &.active {
          background: var(--selected-theme-main-color);
          color: var(--selected-theme-sub-text-color);
          font-weight: 600;
          border-radius: 26px;
        }
      }
    }

    &__cards {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 4rem;
      margin-top: 10px;
      // margin-bottom: 500px;

      &__item {
        height: 300px;
        cursor: pointer;
        position: relative;

        margin-bottom: 200px;

        // &:hover .overlay {
        //   opacity: 1;
        // }

        &__img-wrapper {
          height: 100%;

          a {
            height: 100%;
            img {
              height: 100%;
              object-fit:contain;
              width: 100%;
            }
          }
        }
        .overlay {
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          width: 100%;
          height: 100%;
          opacity: 0;
          transition: 0.5s ease;
          background-color: var(--selected-theme-main-color);


          div {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            text-align: center;

            p{
              color: var(--selected-theme-sub-text-color);
              margin: 0;
              font-weight: 500;
              font-size: 2.5rem;
              position: relative;
              overflow: hidden;
              animation: typingEffect 1s steps(30, end);
              white-space: nowrap;
            }
            h6{
              width: 100%;
              color: var(--selected-theme-sub-text-color);
              margin: 0;
              font-weight: 200;
              font-size: 15px;
              position: relative;
              overflow: hidden;
              animation: typingEffect 1s steps(30, end);
              white-space: nowrap;
            }
            button {
              cursor: pointer;
              // margin-top: 15px;
              padding: 10px 20px;
              font-size: 1.5rem;
              background: transparent;
              color: var(--selected-theme-sub-text-color);
              border: 1.5px solid var(--selected-theme-sub-text-color);
              border-radius: 4px;
              font-weight: 500;
              &:hover {
                color: #000000;
                background:var(--selected-theme-mycolor)
              }
            }
          }
        }
      }
    }
  }
}



